@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Overlock&family=Raleway:wght@300;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1{font-size:4.7rem;font-weight:800;text-transform:uppercase;color:#ffefd6}h2{font-size:1em;font-weight:bold;color:#22031f;text-align:center}h3{font-size:.8em;-webkit-margin-before:1em;margin-block-start:1em;font-weight:bold}h4{color:#f4f3f0;font-size:12px}p{font-size:1.5em;font-weight:bold;padding:.3rem}span{color:#000;font-size:14px}@media only screen and (max-width: 700px){h1{font-size:8vw}h2,svg{font-size:4vw}}*{padding:0;margin:0}.app__container{display:block;background-color:#272e67}.app__header{display:flex;flex-direction:row;justify-content:center;background-color:#272e67;margin-left:14rem}.app__header__title{text-align:center;padding:.5rem;background-color:#272e67;color:#f4f3f0}.app__main{display:flex;flex-direction:row;background-color:#272e67}.app .main{display:inline-flex;flex-direction:column}.app .main__nav-sidebar{display:flex;flex-direction:row;align-items:flex-start;justify-content:center;width:25%}.app .main__beer-gallery{display:flex;flex:wrap 1;flex-direction:column;width:25%}@media screen and (min-width: 961px){.app .punk--title{padding:1.5rem}.app .punk__main{display:flex;flex-direction:row}}
.beerCard{padding:.8rem}.beerCard__container{display:flex;flex:wrap 1;flex-direction:column;justify-content:center;align-items:left;cursor:pointer;background-color:#f4f3f0;color:#22031f;border-radius:12px}.beerCard__container--click{color:#272e67;justify-content:center;align-items:center}.beerCard__name{color:#22031f;font-size:18px}.beerCard__subheading{margin-bottom:1rem}.beerCard__tagLine{display:flex;justify-content:center,space-around;align-items:center}.beerCard__extra{display:flex;flex-direction:row;align-items:center;border:#4db3e9;box-shadow:0rem 0rem .5rem .5rem #f2ecd6;padding:1rem;cursor:pointer;background-color:#f4f3f0;color:#22031f;border-radius:12px;display:flex;flex-direction:row;justify-content:center;align-items:flex-start;margin-top:.5rem;font-size:16px;grid-gap:1.5rem;gap:1.5rem}.beerCard__tile{display:flex;flex-direction:row;justify-content:center;align-items:center;grid-gap:.5rem;grid-gap:.5rem;gap:.5rem;border:solid 9px #272e67;min-height:270px;max-height:300px;border-radius:12px}.beerCard__tile--picture{justify-content:flex-start;margin-right:1.5rem;margin-left:2rem}.beerCard__tile--information{display:flex;flex-direction:column;justify-content:center;margin-right:1rem}.beerCard__tile--informationname{display:flex;align-items:center;justify-content:center}.beerCard__tile--information-tagLine{display:flex;align-items:center;padding-left:10%}.beerCard__picture{display:flex;align-items:center;width:3rem;justify-content:center}.beerCard__data{display:flex;flex-direction:column;justify-content:center;padding:.5rem;margin-top:1rem;border-radius:10px;background-color:#272e67;color:#f4f3f0}.beerCard__description-text{display:flex;flex-direction:column;justify-content:center;align-items:flex-start;margin-top:.5rem;font-size:12px}.data{justify-content:center}@media screen and (min-width: 961px){.card__container:hover{background-color:#f4f3f0}.card__container{min-height:270px;max-height:300px}}
.beerGallery{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:flex-end;align-content:right;margin-left:10rem;background-color:#4db3e9;border-radius:12px;border:solid 7px #f4f3f0;padding:.5rem}@media screen and (min-width: 961px){.beerGallery{display:grid;grid-template-columns:1fr 1fr 1fr;margin:1rem}}
.nav-bar__header{display:flex;justify-content:center;padding:1rem;margin-top:2rem;color:#f4f3f0}.nav-bar__container{background-color:#272e67;display:flex;flex-direction:column;align-items:center;min-height:none;margin-right:.4rem}.nav-bar__filter-boxes{padding:.5rem}@media screen and (min-width: 961px){.nav-bar__container{min-height:100vh}.nav-bar__checkboxes{margin:2rem}}
.searchbox__container{display:flex;flex-direction:row;justify-content:center;margin-left:2rem;margin-bottom:1rem}.searchbox__container--input{padding:1rem;width:15rem;border:1px solid #272e67}@media screen and (min-width: 961px){.searchbox--input{margin:1rem}}
.filterbox__container{display:flex;flex-direction:row;grid-gap:1rem;gap:1rem;justify-content:flex-start;padding-bottom:1.5rem;padding-left:2.5rem;color:#f4f3f0}@media screen and (min-width: 961px){.filterblock__container{width:15rem;margin:.5rem}}
